@mixin font-source($font: false, $size: false, $colour: false, $weight: false, $lh: false) {
  @if $font {
    font-family: $font, sans-serif;
  }
  @if $size {
    font-size: $size;
  }
  @if $colour {
    color: $colour;
  }
  @if $weight {
    font-weight: $weight;
  }
  @if $lh {
    line-height: $lh;
  }
}
